// Library
import React from 'react'

// PUBLIC components
import Masonry from 'react-masonry-component';

// PRIVATE components
import ModalWrapper from '../ModalWrapper'
import * as Styled from './style'

const masonryOptions = {
    transitionDuration: 0
};
 
const imagesLoadedOptions = { background: '.my-bg-image-el' }

class Questions extends React.Component {
    render() {
        return (
            <ModalWrapper active={this.props.show} closeEvent={() => this.props.closeModal()} className={"no-flex-mobile"}> 
                     <Masonry
                className={'container col'} // default ''
                elementType={'ul'} // default 'div'
                options={masonryOptions} // default {}
                disableImagesLoaded={false} // default false
                updateOnEachImageLoad={false} // default false and works only if disableImagesLoaded is false
                imagesLoadedOptions={imagesLoadedOptions} // default {}
            >
                {Object.values(this.props.list || {}).map((val) =>  <Styled.Card className="col--m4 col--s12"><h3>{val.name}</h3><p>{val.quest}</p></Styled.Card>)}
            </Masonry>
            </ModalWrapper>
        )
    }
}

export default Questions