// Library
import React from 'react'

// STYLED components
import * as Styled from './style'
import { X } from 'react-feather'

// 
const ModalWrapper = ({ children, active, closeEvent, className = "" }) => (
    <Styled.Wrapper className={`${active ? 'active' : ''} ${className}`}><div className="wrapper"><Styled.Close className="btn" onClick={() => closeEvent()}><X /></Styled.Close>{ children }</div></Styled.Wrapper>
)

export default ModalWrapper