// Library
import React from 'react'

// STYLED components
import * as Style from './style'

// LIBRARY components
import { Grid, HelpCircle } from 'react-feather'

class Navigation extends React.Component {
    render() {
        return (
            <Style.Wrapper>
               <Style.Button onClick={() => this.props.showQuestion()}><Grid /></Style.Button> 
               <Style.Button onClick={() => this.props.showAsk()}><HelpCircle /></Style.Button> 
            </Style.Wrapper>
        )
    }
}

export default Navigation