import React from 'react';
import Helmet from 'react-helmet';
import { ProjectListing, Layout } from 'components';

// Library Components
import Slider from 'react-slick'

// HELPERS
import { base } from '../constants/firebase'

// PRIVATE components
import Navigation from '../components/containers/Present/Navigation'
import Ask from '../components/containers/Present/Ask'
import Question from '../components/containers/Present/Questions'

// SLIDES component
import First from '../components/containers/Present/Slides/First/';
import Second from '../components/containers/Present/Slides/Second';
import Third from '../components/containers/Present/Slides/Third';
import Fourth from '../components/containers/Present/Slides/Fourth';
import Fifth from '../components/containers/Present/Slides/Fifth';
import Sixth from '../components/containers/Present/Slides/Sixth';
import Seventh from '../components/containers/Present/Slides/Seventh';

class About extends React.Component {
    constructor() {
        super();

        this.state = {
            slide: {
                num: 1,
                content: {},
                asks: {}
            },
            modal: {
                ask: false,
                question: false,
            },
            questions: {}
        }
    }

    addSlide = (num) => {
        const slide = { ...this.state.slide }
        slide['num'] = num;

        this.setState({ slide })
    }

    submitAsk = (name, quest) => {
        const slide = { ...this.state.slide }
        const id = Date.now();
        slide['questions'] = { 
            ...slide['questions'],
            [id] : {
                name,
                quest
            }
        }

        this.setState({ slide })
    }

    componentWillMount() {
        this.slideRef = base.syncState('slide', {
            context: this,
            state: 'slide'
        })
    }

    componentDidMount() {
        let that = this
        base.listenTo('slide', {
            context: this,
            then(val) {
                that.slider.slickGoTo(val.num - 1)
            }
        })
    }

    componentWillUnmount() {
        base.removeBinding(this.slideRef);
    }

    triggerModal = (type, condition) => {
        const modal = { ...this.state.modal }
        modal[type] = condition

        this.setState({ modal })
    }

    render() {
       const settings = {
           dots: false,
           infinite: true,
           speed: 500,
           slidesToShow: 1,
           slidesToScroll: 1,
           draggable: false,
       }
       return (
            <Layout nofooter>
                <Helmet title={`PRESENT | FRONTSIDE THINGS`} />
                    <div className="container container--presentation">
                        <Slider ref={(slider) => (this.slider = slider)} {...settings}>
                            <First />
                            <Sixth />
                            <Seventh />
                            <Third />
                            <Fifth />
                            <Fourth />
                            <Second />
                        </Slider>
                    </div>
                    <Ask show={this.state.modal.ask} closeModal={() => this.triggerModal('ask', false)} submit={(name,quest) => this.submitAsk(name, quest)}/>
                    <Navigation showAsk={() => this.triggerModal('ask', true)} showQuestion={() => this.triggerModal('question', true)}/>
                    <Question show={this.state.modal.question} closeModal={() => this.triggerModal('question', false)} list={this.state.slide.questions} />
            </Layout>
        );
    }
}

export default About;